<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/rolling-banner')">메인 배너</li>
			<li @click="goto('/admin/introduction')" class="current">
				운동친구 소개
			</li>
			<li @click="goto('/admin/admin-member')">운동친구 멤버</li>
			<li @click="goto('/admin/faq')">자주묻는질문</li>
			<li @click="goto('/admin/terms')">약관</li>
		</ul>

		<div class="contents-container">
			<p class="contents-title underline marB-20 body3">운동 친구 소개페이지</p>
			<div class="input-container marB-40">
				<section class="marB-40">
					<p class="body3 marB-20">미션과 비전</p>

					<div class="input-item">
						<p class="small-title">운동 친구 소개글</p>
						<textarea class="admin-input large" rows="3" v-model="contents" />
					</div>
					<div class="multiple-images">
						<div v-if="missionList.imgPath1" class="file-preview-wrapper">
							<label for="file"> <img :src="missionList.imgPath1"/></label>
							<div class="img-info">
								<span>{{ missionList.imgName1 }}</span>

								<div @click="fileDeleteButtonImg1">
									<Close />
								</div>
							</div>
						</div>
						<AddImageMultiple v-else :refName="'image1'" @image1="setImage1" />
						<div v-if="missionList.imgPath2" class="file-preview-wrapper">
							<label for="file"> <img :src="missionList.imgPath2"/></label>
							<div class="img-info">
								<span>{{ missionList.imgName2 }}</span>

								<div @click="fileDeleteButtonImg2">
									<Close />
								</div>
							</div>
						</div>
						<AddImageMultiple v-else :refName="'image2'" @image2="setImage2" />
						<div v-if="missionList.imgPath3" class="file-preview-wrapper">
							<label for="file"> <img :src="missionList.imgPath3"/></label>
							<div class="img-info">
								<span>{{ missionList.imgName3 }}</span>

								<div @click="fileDeleteButtonImg3">
									<Close />
								</div>
							</div>
						</div>
						<AddImageMultiple v-else :refName="'image3'" @image3="setImage3" />
					</div>
					<div class="input-item align-top">
						<button class="outlined-btn" @click="updateMission(1)">
							저장
						</button>
					</div>
				</section>

				<section class="marB-40">
					<p class="body3 marB-20">활동 기록</p>
					<div
						v-for="(historyItem, index) in historyList"
						:key="historyItem.id"
					>
						<div class="input-item marB-20">
							<p class="small-title">활동 {{ index + 1 }}</p>
							<div class="input-item-body">
								<input
									type="text"
									class="admin-input small"
									name="archive_date"
									placeholder="YYYY-MM-DD"
									v-model="historyItem.historyDate"
								/>
								<input
									type="text"
									class="admin-input medium"
									name="contents"
									placeholder="활동 내용"
									v-model="historyItem.contents"
								/>
								<div
									@click="removeField(historyItem.id ? historyItem.id : index)"
								>
									<CloseRed />
								</div>
							</div>
						</div>
					</div>

					<button class="solid-btn green marB-30" @click="addField">
						활동기록 추가
					</button>
					<button class="outlined-btn block" @click="updateHistory">
						저장
					</button>
				</section>

				<section class="marB-40">
					<div class="img-upload-container">
						<p class="body3">운동친구 BI(웹)</p>
						<div class="input-item align-top">
							<div v-if="webImage.imgPath" class="file-preview-wrapper">
								<label for="file"> <img :src="webImage.imgPath"/></label>
								<div class="img-info">
									<span>{{ webImage.imgName }}</span>

									<div @click="fileDeleteButtonWeb">
										<Close />
									</div>
								</div>
							</div>
							<AddImageSingle
								v-else
								:image="webImage"
								:refName="'webImage'"
								@webImage="setWebImage"
							/>
							<button class="outlined-btn" @click="updateBi(1)">
								저장
							</button>
						</div>
					</div>
					<div class="img-upload-container">
						<p class="body3">운동친구 BI(모바일)</p>
						<div class="input-item align-top">
							<div v-if="mobileImage.imgPath" class="file-preview-wrapper">
								<label for="file"> <img :src="mobileImage.imgPath"/></label>
								<div class="img-info">
									<span>{{ mobileImage.imgName }}</span>

									<div @click="fileDeleteButtonMobile">
										<Close />
									</div>
								</div>
							</div>
							<AddImageSingle
								v-else
								:refName="'mobileImage'"
								@mobileImage="setMobileImage"
							/>
							<button class="outlined-btn" @click="updateBi(2)">
								저장
							</button>
						</div>
					</div>
				</section>
			</div>
		</div>
	</div>
</template>

<script>
import {
	fetchMissionAll,
	updateMission,
	updateHistory,
	deleteHistory,
	updateBi,
} from '@/api/intro';
import AddImageSingle from '@/components/common/AddImageSingle';
import { mapGetters } from 'vuex';
import Store from '@/store';
import CloseRed from '@/assets/icon/program_goods/CloseRed';
import AddImageMultiple from '@/components/common/AddImageMultiple';
import Close from '@/assets/icon/Close';

export default {
	components: { AddImageSingle, CloseRed, AddImageMultiple, Close },
	computed: {
		...mapGetters(['web', 'mobile', 'image1', 'image2', 'image3']),
	},
	created() {
		Store.commit('SET_IMAGE1', null);
		Store.commit('SET_IMAGE2', null);
		Store.commit('SET_IMAGE3', null);
		Store.commit('SET_WEB', null);
		Store.commit('SET_MOBILE', null);
		this.fetchMissionAll();
	},
	data() {
		return {
			biList: [],
			historyList: [
				{
					historyDate: '',
					contents: '',
				},
			],
			contents: '',
			missionList: [],
			mobileImage: '',
			webImage: '',
		};
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async fetchMissionAll() {
			const { data } = await fetchMissionAll();
			this.biList = data.biList;
			this.webImage = data.biList[0];
			this.mobileImage = data.biList[1];
			this.historyList = data.historyList;
			this.missionList = data.missionList[0];
			console.log(this.missionList);
		},
		addField() {
			this.historyList.push({
				historyDate: '',
				contents: '',
			});
		},
		async removeField(id) {
			try {
				if (id) {
					let data = [id];
					await deleteHistory(data);
				}
			} catch (e) {
				console.log(e);
			}
			this.historyList.splice(id, 1);
		},
		async updateMission(id) {
			let data = new FormData();
			data.append('contents', this.contents);
			data.append('file1', this.image1);
			data.append('file2', this.image2);
			data.append('file3', this.image3);
			try {
				await updateMission(id, data);
			} catch (e) {
				console.log(e);
			}
		},

		async updateHistory() {
			try {
				await updateHistory(this.historyList);
			} catch (e) {
				console.log(e);
			}
		},
		async updateBi(id) {
			let data = new FormData();
			data.append('id', id);
			if (id == 1) {
				data.append('file', this.web);
			} else if (id === 2) {
				data.append('file', this.mobile);
			}

			try {
				await updateBi(id, data);
			} catch (e) {
				console.log(e);
			}
		},
		initForm() {
			this.contents = '';
			Store.commit('SET_IMAGE1', '');
			Store.commit('SET_IMAGE2', '');
			Store.commit('SET_IMAGE3', '');
			Store.commit('SET_WEB', '');
			Store.commit('SET_MOBILE', '');
		},
		setImage1(data) {
			Store.commit('SET_IMAGE1', data);
		},
		setImage2(data) {
			Store.commit('SET_IMAGE2', data);
		},
		setImage3(data) {
			Store.commit('SET_IMAGE3', data);
		},
		setWebImage(data) {
			Store.commit('SET_WEB', data);
		},
		setMobileImage(data) {
			Store.commit('SET_MOBILE', data);
		},
		fileDeleteButtonImg1() {
			this.missionList.imgPath1 = '';
			this.missionList.imgName1 = '';
		},
		fileDeleteButtonImg2() {
			this.missionList.imgPath2 = '';
			this.missionList.imgName2 = '';
		},
		fileDeleteButtonImg3() {
			this.missionList.imgPath3 = '';
			this.missionList.imgName3 = '';
		},
		fileDeleteButtonWeb() {
			this.webImage.imgPath = '';
			this.webImage.imgName = '';
		},
		fileDeleteButtonMobile() {
			this.mobileImage.imgPath = '';
			this.mobileImage.imgName = '';
		},
	},
};
</script>

<style lang="scss" scoped>
.input-item-body {
	display: flex;
	align-items: center;
}
.multiple-images {
	display: flex;
}
textarea.admin-input {
	height: 150px;
}
.file-preview-wrapper {
	position: relative;
	display: inline-block;
	width: 100px;
	margin-right: 10px;
	img {
		width: 100px;
		height: 100px;
		border: 2px solid #bebebe;
		box-sizing: border-box;
	}
}
</style>
