// 메인화면

import { instance, instanceWithAuth } from './index';
function fetchMissionAll() {
	return instance.get('api/mission/allByMap');
}

function updateMission(id, data) {
	return instanceWithAuth.put(`api/admin/mission/update/${id}`, data);
}

function addHistory(data) {
	return instanceWithAuth.post('api/admin/history/new', data);
}

function updateHistory(data) {
	return instanceWithAuth.put(`api/admin/history/update`, data);
}

function deleteHistory(data) {
	return instanceWithAuth.delete(`api/admin/history/delete`, data);
}

function updateBi(id, data) {
	return instanceWithAuth.put(`api/admin/businessIdentity/update/${id}`, data);
}

export {
	fetchMissionAll,
	updateMission,
	addHistory,
	updateHistory,
	deleteHistory,
	updateBi,
};
